<template>
	<div class="footer-bar">
		<div class="logo-wrap">
			<img class="gold-logo" src="../assets/BottomLogo.png"/>
			<span>TEAMCATALYST.TV</span>
			<a href="./CodeOfConduct" class="yellow">Code of Conduct</a>
			<a href="mailto:manager@teamcatalyst.tv" class="yellow">Contact us</a>
		</div>
		<div class="social">
			<social-media-icons :iconColor="'#4f5154'" :size="'20px'"></social-media-icons>
		</div>
	</div>
</template>

<script>
import SocialMediaIcons from '../components/SocialMediaIcons'
export default {
	name: 'footer-bar',
	components: {SocialMediaIcons}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../variables.scss";
a{
	margin: 0 0 0 15px;
}
a:hover{
	color: $yellow-light;
}
.footer-bar{
  position: absolute;
	background-color: $gray-dark;
	padding: 30px;
  right: 0;
  bottom: 0;
  left: 0;
}
.logo-wrap{
	display: inline-block;
	position: absolute;
	left: 5%;
	top: 7%;
	transition: all .2s ease-in-out;
}
.gold-logo{
	width:75px;
	vertical-align: middle;
}
.social{
	display: inline-block;
	position: absolute;
	right: 5%;
	top: 11%;
	transition: all .2s ease-in-out;
	ul{
		margin: 0;
	}
}
@media screen and (max-width: 70.000em){ /*960px*/
	.social{
		right: 2%;
	}
	.logo-wrap{
		left: 2%;
	}
}
@media screen and (max-width: 55.000em){ /*960px*/
	.social{
		display: none;
	}
	a{
		margin: 0 0 0 5px;
	}
}
</style>


