<template>
	<div class="event-info-panel">
		<div class="event-header">
			<h2 class="event-owner">Event Details</h2>
			<h2 class="event-owner">{{eventOwner.display_name}}</h2>
		</div>
		<img :src="logo" class="event-profile-pic">
	</div>
</template>

<script>
import Moment from 'moment'
import Members from '../../../static/memberInfo.json'
import SectionHeader from '../home/SectionHeader'

export default {
	name: 'event-info-panel',
	data () {
		return {
			sectionName: 'CALENDAR'
		}
	},
	computed: {
		weekDate: function () {
			return this.event.start_dt ? Moment(this.event.start_dt).format('dddd').toUpperCase() : ''
		},
		day: function () {
			return this.event.start_dt ? Moment(this.event.start_dt).format('D') : ''
		},
		month: function () {
			return this.event.start_dt ? Moment(this.event.start_dt).format('MMMM').toUpperCase() : ''
		},
		eventTitle: function () {
			return this.event.title ? this.event.title : ''
		},
		logo: function () {
			return this.$store.state.TwitchInfo[this.eventOwner.id] ? this.$store.state.TwitchInfo[this.eventOwner.id].logo : ''
		},
		eventOwner: function () {
			if (this.event.subcalendar_ids) {
				for (var member in Members) {
					if (Members[member].calendar_id === this.event.subcalendar_ids[0]) {
						return Members[member]
					}
				}
			}
			return ''
		}
	},
	methods: {
		getTime: function (event) {
			return this.event.start_dt ? Moment(this.event.start_dt).format('LT') : ''
		}
	},
	components: {
		SectionHeader
	},
	props: ['event']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
.event{
	&-info-panel{
		display: table-cell;
		background-color: $gray-light;
    vertical-align: top;
	}
	&-header{
		text-align: left;
		vertical-align: middle;
		background-color: $yellow-dark;
	}
	&-owner{    
		display: inline-block;
    margin: 0;
		color: $gray-dark;
		line-height: 75px;
	}
	&-profile-pic{
		width: 75px;
		float: right;
	}
}
</style>


