import Vue from 'vue'
import config from '../../config/config.json'
var teamUpHeader = {headers: {'Teamup-Token': config.team_up.api_key}}

export default {
	getGroupCalendar (calendarKey, startDate, endDate) { // Date format YYYY-MM-DD
		var url = 'https://api.teamup.com/' + calendarKey + '/events?startDate=' + startDate + '&endDate=' + endDate
		var p1 = new Promise(function (resolve, reject) {
			Vue.http.get(url, teamUpHeader).then(response => {
				resolve(response)
			}, response => {
				// error goes here
				reject(response)
			})
		})
		return p1
	}
}



