<template>
<div class='calendarWrapper'>
	THIS IS WHERE THE CALENDAR GOES AND I HAVE TO IMPORT IT SOMEHOW
	<div id="calendar"></div>
</div>
</template>

<script>
// import that global data store
import { mapGetters, mapActions } from 'vuex'

export default {
	name: 'twitch-embed',
	data () {
		return {
		}
	},
	methods: {
		loadCalendar: function () {
		},
		...mapActions([
			'updateChannelInfoList'
		])
	},
	computed: {
		...mapGetters([
			'twitchInfo'
		])
	},
	mounted () {
		this.loadCalendar()
	}
}
</script>

<style scoped lang="scss">
#calendar{
	width: 65%;
}
</style>



