<template>
	<div>
		<header-bar :showGold="false"></header-bar>
		<div class="twitch-wrap"><twitch-embed :target="streamer"></twitch-embed></div>
		<footer-bar></footer-bar>
	</div>
</template>

<script>
import TwitchEmbed from '../components/stream/TwitchEmbed'
import HeaderBar from './Header'
import FooterBar from './Footer'

export default {
	name: 'stream',
	components: {
		TwitchEmbed,
		HeaderBar,
		FooterBar
	},
	props: ['streamer']
}
</script>

<style scoped lang="scss">
@import "../variables.scss";
.twitch-wrap{
	padding-bottom: 32px;
}
</style>


