<template>
	<div class="about">
		<header-bar :showGold="false"></header-bar>
		<calendar></calendar>
		<footer-bar></footer-bar>
	</div>
</template>

<script>
import Members from '../../static/memberInfo.json'
import HeaderBar from './Header'
import Calendar from './events/Calendar'
import FooterBar from './Footer'
import { mapActions } from 'vuex'

export default {
	name: 'about',
	components: {
		HeaderBar,
		Calendar,
		FooterBar
	},
	data () {
		return {
			streamList: {},
			odyddey: null,
			memberInfo: Members
		}
	},
	methods: {
		// in here we stitch together the profile and the stream info
		getMemeberInfo: function (name) {
			if (name) {
				var member = this.memberInfo[name.display_name.toLowerCase()]
				member.channel_info = name
				return member
			} else {
				return null
			}
		},
		...mapActions([
			'updateChannelInfoList',
			'getChannelInfo'
		])
	},
	mounted () {
		this.updateChannelInfoList().then(function () {
			this.streamList = this.$store.state.TwitchInfo
		}.bind(this))
		this.getChannelInfo(this.memberInfo.odysseygrrrl.id).then(function (result) {
			this.odyddey = result
		}.bind(this))
	}
}
</script>

<style scoped lang="scss">
@import "../variables.scss";
.about{
	background-color: $gray-med;
	padding-bottom: 60px;
}
.tile-wrapper{
	width: 75%;
	margin: auto;
}
</style>



