<template>
	<div class="member-tile" v-bind:id="member.display_name+'-tile'">
		<div>
			<img :src="member.channel_info.logo">
			<div class="member-tile-info">
				<div>
					<h2>{{member.display_name}}</h2>
					<div class="live" v-show="member.channel_info.live">live! {{viewers}}
	          <svg height="16px" version="1.1" viewBox="0 0 16 16" width="16px" x="0px" y="-10px" class="svg-glyph_live">
							<path clip-rule="evenodd" d="M11,14H5H2v-1l3-3h2L5,8V2h6v6l-2,2h2l3,3v1H11z" fill-rule="evenodd"></path>
						</svg>
	        </div>
        </div>
				<div v-if="member.role==='streamer'">
					<a class="yellow" :href="member.channel_info.url">
						{{member.channel_info.url.slice(12)}}
					</a>
					<span>{{followers}}</span>
					<span class="no-margin yellow">{{member.schedule}}</span>
					<span class="no-margin yellow" v-if="member.schedule2">{{member.schedule2}}</span>
					<br v-else />
				</div>
				<div v-else>
					<h2 class="role">{{member.role}}</h2>
					<a :href="member.channel_info.url">
						{{member.channel_info.url.slice(12)}}
					</a>
					<a :href="member.social_media.email" class="manager-email yellow">{{member.email}}</a>
					<br/>
				</div>
			</div>
		</div>
		<p>{{member.bio}}</p>
		<div class="member-tile-social">
			<div v-for="(value, key) in member.social_media" class="icon" :class="key+'-wrap'">
				<a :href="value" :title="key" :class="key"></a>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'member-tile',
	data () {
		return {
			streamList: {}
		}
	},
	computed: {
		schedule: function () {
			return this.member.schedule
		},
		schedule2: function () {
			return this.member.schedule2
		},
		viewers: function () {
			return this.member.channel_info.live ? this.member.channel_info.live.viewers.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') : 0
		},
		followers: function () {
			return this.member.channel_info.followers ? this.member.channel_info.followers.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') + ' Followers' : ''
		}
	},
	props: ['member']
}
</script>

<style scoped lang="scss">
@import "../../variables.scss";
a{
	display:block;
	margin: 5px 0;
}
h2{
	display: inline-block;
	color: $gray-light;
}
span{
	display: block;
	margin: 7px 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.live{
	vertical-align: top;
}
.role{
	color: $yellow-dark;
	font-size: 20px;
	margin: 1px 0;
}
.no-margin{
	margin:0;
}
.manager-email{
	margin:0;
	padding: 2px 0 0 0;
}
.svg-glyph_live {
	vertical-align: middle;
}
.member-tile {
	width: 325px;
	margin: auto;
	display: inline-block;
	margin: 10px;
	padding: 10px;
	vertical-align: top;
	img {
		width: 100px;
		margin: 5px;
		display: inline-block;
		vertical-align: bottom;
		border-radius: 1000px;
		padding: 3px;
	}
	&-info{
		margin: 0 auto;
		vertical-align: top;
	}
	&-social{
		margin: 5px 0;
	}
}
#lowco2525-tile img{
	border: 2px solid $lowco-purple;
}
#smashley-tile img{
	border: 2px solid $smashley-blue;
}
#djtechlive-tile img{
	border: 2px solid $dj-blue;
}
#bloodyfaster-tile img{
	border: 2px solid $bloody-red;
}
#odysseygrrrl-tile img{
	border: 2px solid $yellow-dark;
}
.icon {
	display: inline-block;
	margin-right: 10px;
	transition: all .1s ease-in-out;
	a{
		width: 25px;
		height: 25px;
	}
	&:hover{
	-webkit-filter: drop-shadow(0 0 1px $font-white);
	-moz-filter: drop-shadow(0 0 1px $font-white);
	-ms-filter: drop-shadow(0 0 1px $font-white);
	-o-filter: drop-shadow(0 0 1px $font-white);
		filter: drop-shadow(0 0 1px $font-white);
	}
	.twitter {
		background: url("../../assets/twitter.svg") no-repeat 50% 50%; 
		background-size: cover;
	}
	.discord {
		background: url("../../assets/discord.svg") no-repeat 50% 50%; 
		background-size: cover;
	}
	.email {
		background: url("../../assets/email.svg") no-repeat 50% 50%; 
		background-size: cover;
	}
	.instagram {
		background: url("../../assets/instagram.svg") no-repeat 50% 50%; 
		background-size: cover;
	}
	.youtube {
		background: url("../../assets/youtube.svg") no-repeat 50% 50%; 
		background-size: cover;
	}
}
</style>


