<template>
	<div class="events">
		<section-header :sectionName="sectionName" class="custom-header"></section-header>
		<div class="section-wrapper">
			<!--<event-info :event="selectedEvent"></event-info :event="selectedEvent">-->
			<div class="events-wrapper">
				<uL class="events-date-list">
					<li class="events-date" v-for="(value, key) in calendarGroupedByDay">
						<date-tile :date="value" :dateString="key"></date-tile>
					</li>
				</ul>
			</div>
			<event-filters v-on:filter="filterList" class="filter-list"></event-filters>
		</div>
	</div>
</template>

<script>
import { mapActions } from 'vuex'
import SectionHeader from './SectionHeader'
import DateTile from './DateTile'
import EventInfo from '../events/EventInfo'
import EventFilters from '../events/EventFilters'
import Moment from 'moment'
import Members from '../../../static/memberInfo.json'

export default {
	name: 'events',
	data () {
		return {
			calendarInfo: {},
			calendarGroupedByDay: {},
			sectionName: 'CALENDAR'
		}
	},
	components: {
		SectionHeader,
		DateTile,
		EventInfo,
		EventFilters
	},
	methods: {
		parseCalendars: function (calendarData, targetWeek) {
			var newGrouping = {}
			for (var i = 0; i < calendarData[targetWeek].events.length; i++) {
				var event = calendarData[targetWeek].events[i]
				var eventStart = Moment(event.start_dt)
				// check to see if the event has started
				if (eventStart.isBefore(Moment())) {
					for (var member in Members) {
						if (event.subcalendar_ids[0] === Members[member].calendar_id) {
							if (this.$store.state.TwitchInfo[Members[member].id]) {
								event.title = member + ' - ' + this.$store.state.TwitchInfo[Members[member].id].game
							}
						}
					}
				}
				var eventEnd = Moment(event.end_dt)
				event.show = true
				for (var m = eventStart; m.isBefore(eventEnd); m.add(1, 'days')) {
					var eventDate = eventStart.format('YYYY-MM-DD')
					if (newGrouping[eventDate]) {
						newGrouping[eventDate].push(event)
					} else {
						newGrouping[m.format('YYYY-MM-DD')] = [event]
					}
				}
			}
			// handle next day all day event bug
			// Checks to see if the last day is just an all day event, there's only one of them and if the day is past the day limit and deletes it until the api is updated
			var lastDay = newGrouping[Object.keys(newGrouping)[Object.keys(newGrouping).length - 1]]
			if (lastDay.length === 1 && lastDay[0].all_day && Moment(targetWeek).add(6, 'days').format('YYYY-MM-DD') !== Moment(lastDay[0].start_dt).format('YYYY-MM-DD')) {
				delete newGrouping[Object.keys(newGrouping)[Object.keys(newGrouping).length - 1]]
			}
			this.calendarGroupedByDay = newGrouping
		},
		filterList: function (target) {
			for (var date in this.calendarGroupedByDay) {
				var day = this.calendarGroupedByDay[date]
				for (var event in day) {
					if (day[event].subcalendar_ids[0] === target) {
						day[event].show = !day[event].show
					}
				}
			}
		},
		...mapActions([
			'updateGroupCalendar'
		])
	},
	computed: {
		selectedEvent: function () {
			var today = Moment().format('YYYY-MM-DD')
			return this.calendarGroupedByDay[today] ? this.calendarGroupedByDay[today][0] : {}
		}
	},
	mounted () {
		var startDate = Moment().startOf('day').format('YYYY-MM-DD')
		if (this.$store.state.CalendarInfo[startDate]) {
			this.calendarInfo = this.$store.state.CalendarInfo
			this.parseCalendars(this.calendarInfo, startDate)
		} else {
			var endDate = Moment().endOf('day').add(6, 'days').format('YYYY-MM-DD')
			this.updateGroupCalendar({'startDate': startDate, 'endDate': endDate}).then(function () {
				this.calendarInfo = this.$store.state.CalendarInfo
				this.parseCalendars(this.calendarInfo, startDate)
			}.bind(this))
		}
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
.section-wrapper{
	display: block;
	width: 70%;
	max-width: 1100px;
	margin: 0 auto;
	padding-bottom: 100px;
}
.events{
	background-color: $gray-med;
	text-align: left;
	vertical-align: top;
	&-wrapper{
		display: table-cell;
	}
	&-date{
		display: inline-block;
		vertical-align: top;
		padding: 10px 0;
		max-width: 375px;
		&-list{
			display: inline-block;
			column-count: 2;
			-moz-column-count: 2;
			-webkit-column-count: 2;
			column-width: 350px;
			-moz-column-width: 350px;
			-webkit-column-width: 350px;
			text-align:left;
			margin: auto;
			padding: 0;
			transition: all .2s ease-in-out;
		}
	}
}
@media screen and (max-width: 80.000em){
	.section-wrapper{
		width: 90%;
		padding: 10px;
	}
}
@media screen and (max-width: 60.000em){
	.events-date-list{
		text-align:center;
	}
}
</style>


