<template>
	<div class="event-filters-panel">
		<div v-for="streamer in members" class="filter-wrap" :class="getFilterType(streamer)" v-show="streamer.display_name !== 'odysseygrrrl'">
			<a v-on:click="filterList(streamer.display_name)">
				<span class="arrow-right" :class="{filtered : filterState[streamer.display_name]}"></span>
				<img :src="getLogo(streamer)" class="filter-img"/>
			</a>
		</div>
	</div>
</template>

<script>
import Members from '../../../static/memberInfo.json'

export default {
	name: 'event-info-panel',
	data () {
		return {
			sectionName: 'CALENDAR',
			members: Members,
			filterState: {}
		}
	},
	methods: {
		getFilterType: function (streamer) {
			if (streamer.display_name === 'odysseygrrrl') {
				return 'team-event'
			}
			return streamer.display_name
		},
		getLogo: function (streamer) {
			return this.$store.state.TwitchInfo[streamer.id] ? this.$store.state.TwitchInfo[streamer.id].logo : './static/logo.png'
		},
		filterList: function (target) {
			var tempState = this.filterState
			this.filterState = {}
			tempState[target] = !tempState[target]
			this.filterState = tempState
			this.$emit('filter', this.members[target].calendar_id)
		}
	},
	mounted () {
		for (var streamer in this.members) {
			if (streamer === 'odysseygrrrl') {
				streamer = 'team-event'
			}
			this.filterState[streamer] = false
		}
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
.event-filters-panel{
	display:table-cell;
	vertical-align: top;
	.arrow-right {
		display: inline-block;
		border-top: 5px solid transparent;
		border-bottom: 5px solid transparent;
		border-left: 10px solid $yellow-dark;
		margin-bottom: 30px;
	}
	.filtered {
		border-left: 10px solid $gray-light;
	}
}
.filter{
	&-img{
		display: inline-block;
		width: 75px;
		background-color: $gray-dark;
	}
	&-wrap{
		white-space: nowrap;
		vertical-align: middle;
		cursor: pointer;
		a {
			vertical-align: bottom;
		}
	}
}
@media screen and (max-width: 60.000em){
	.event-filters-panel{
		display: none;
	}
}
</style>


