<template>
	<div id="nav-wrapper">
		<div id="gold-header" v-show="showGold">
			<div class="logo-wrap">
				<img class="gold-logo" src="../assets/Logotop1.png"/>
				<span>TEAMCATALYST.TV</span>
			</div>
			<social-media-icons :iconColor="'#5a5c64'" :size="'20px'" class="social"></social-media-icons>
		</div>
		<nav id="nav-bar">
			<a class='logo' href="./#/"><img src="../assets/Logoweb1.png" /></a>	
			<ul class="menu">
				<a href="./#/streams"><li class="link">STREAMS</li></a><a href="./#/events/"><li class="link">EVENTS</li></a><a href="https://www.twitch.tv/team/catalyst/"><li class="link">TWITCH</li></a><a href="./#/about"><li class="link">ABOUT US</li></a>
			</ul>
		</nav>
	</div>
</template>

<script>
import SocialMediaIcons from '../components/SocialMediaIcons'

export default {
	name: 'header-bar',
	props: ['showGold'],
	components: {SocialMediaIcons}
}
window.onscroll = function () {
	var scrollTop = (window.pageYOffset !== undefined) ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop
	if (scrollTop < 400) {
		document.getElementById('gold-header').style['margin-bottom'] = '0'
		document.getElementById('nav-bar').style.position = 'initial'
	} else if (scrollTop >= 400 && scrollTop < 600) {
		document.getElementById('nav-bar').style.top = -100
	} else {
		document.getElementById('nav-bar').style.position = 'fixed'
		document.getElementById('gold-header').style['margin-bottom'] = '70px'
		document.getElementById('nav-bar').style.top = 0
	}
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
a{
	text-decoration: none;
}
.link{
	display: inline-block;
	cursor: pointer;
	padding: 10px;
	line-height: 52px;
	width: 85px;
	cursor: pointer;
	color: $yellow-dark;
	transition: all .3s ease-in-out;
	&:hover{
		background-color: $yellow-dark;
		color: $gray-dark;
	}
	&:active{
		background-color: $yellow-light;
		color: $gray-dark;
	}
}
#nav{
	&-wrapper{
		min-height: 70px;
	}
	&-bar{
		position: initial;
		text-align: center;
		transition: all .5s ease-in-out;
		min-height: 70px;
		width: 100%;
		background-color: $gray-dark;
		top: -100;
		z-index: 1;
		min-width: 500px;
	}
}
#gold-header{
	background-color: $yellow-dark;
	background-image: url('../assets/Topbar1.png');
	position:relative;
	height: 100px;
}
.social{
	display: inline-block;
	position: absolute;
	right: 5%;
	top: 10%;
}
.gold-logo{
	width:75px;
	vertical-align: middle;
}
.menu{
	display: inline-block;
	position: absolute;
	right: 35px;
	font-size: 14px;
	white-space: nowrap;
	-webkit-margin-before: 0;
	-webkit-margin-after: 0;
	-webkit-margin-start: 0;
	-webkit-margin-end: 0;
	-webkit-padding-start: 0;
	margin: 0;
	padding: 0;
	a{
		margin: 0;
	}
}
.logo{
	display: inline-block;
	vertical-align: middle;
	margin: 10px !important;
	width: 275px;
	transition: all .3s linear;
	border-radius: 0;
  overflow: hidden;
	img{
		width:275px;
		transition: all .3s linear;
	}
	&-wrap{
		color: $gray-light;
		display: inline-block;
		position: absolute;
		white-space: nowrap;
		right: 95%;
		left: 5%;
		top: 12%;
		font-weight: bolder;
		font-size: 18px;
  	transition: all .3s ease-out;
	}
}
@media screen and (max-width: 77.000em){
	.menu{
		position: initial;
	}
}
@media screen and (max-width: 55.000em){
	.link{
		width: 72px;
	}
}
@media screen and (max-width: 45.000em){
	.list-break{
		margin-left: 100px;
	}
	.logo{
		border-radius: 100px;
		width: 70px;
	}
	.social{
	  visibility: hidden;
	  opacity: 0;
	}
	.menu{
		padding-right: 0;
		right: 0;
		left: 0;
		margin: 0 auto;
	}
	.logo-wrap{
    left: 0;
    right: 0;
	}
	a{
		margin: 0 0 0 5px;
	}
}
</style>


