<template>
	<div id="router">
		<header-bar :showGold="true"></header-bar>
		<team-intro></team-intro>
		<members></members>
		<social-media></social-media>
		<events></events>
		<footer-bar></footer-bar>
	</div>
</template>

<script>
import HeaderBar from '../components/Header'
import TeamIntro from '../components/home/TeamIntro'
import Members from '../components/home/Members'
import SocialMedia from '../components/home/SocialMedia'
import Events from '../components/home/Events'
import FooterBar from '../components/Footer'

export default {
	name: 'home',
	components: {
		HeaderBar,
		TeamIntro,
		Members,
		SocialMedia,
		Events,
		FooterBar
	},
	methods: {
	}
}
</script>

<style lang="scss">
@import '../variables.scss';
html {
  box-sizing: border-box;
}
body {
  margin: 0;
  background-color: $gray-med;
  min-width: 500px;
}
@font-face {
  font-family: 'ratio';
  src: url('../assets/fonts/ratio.woff2') format('woff2'),
       url('../assets/fonts/ratio.woff') format('woff'),
       url('../assets/fonts/ratio.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
.yellow{
	color: $yellow-dark;
}
a.yellow:hover{
	color: $yellow-light;
}
#app {
	font-family: $font-stack;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: $font-white;
	margin: 0;
	letter-spacing: 0.15em;
  position: relative;
	padding-bottom: 6vh;
	min-height: 97vh;
}
.parallax {
	background-image: url('../assets/welcomeBackground.png');
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-size: cover;
}
.live {
	display: inline-block;
	background-color: $live-red;
	padding: 5px 10px;
	border-radius: 10px;
	white-space: nowrap;
}
.svg-glyph_live {
	fill: $font-white;
	vertical-align: top;
}
p{
	color: $font-white;
	margin: 5px;
}
a{
	text-decoration: none;
	color: $font-white;
	margin: 0;
}
ul{
	cursor:default;
}
h1, h2, h3{
	font-weight: lighter;
}
h2, h3{
	color: $yellow-dark;
	font-size: 26px;
	margin: 0;
}
h3{
	font-size: 16px;
	margin: 5px 0;
}
</style>


