<template>
<div class='twitchWrapper'>
	<ul>
		<a v-for="stream in liveStreamList" v-on:click='setTargetStream(stream.display_name)'>
			<li v-bind:class="{ selected: isSelected(stream.display_name), 'not-live': !stream.live }">
				{{stream.display_name}}
				<span v-if='stream.live' class="live" >
					<div v-show="stream.live">{{stream.live.viewers}}
						<svg height="16px" version="1.1" viewBox="0 0 16 16" width="16px" x="0px" y="-10px" class="svg-glyph_live">
							<path clip-rule="evenodd" d="M11,14H5H2v-1l3-3h2L5,8V2h6v6l-2,2h2l3,3v1H11z" fill-rule="evenodd"></path>
						</svg>
					</div>
				</span>
			</li>
		</a>
	</ul>
	<div class="wrapper">
		<div class='twitchStream'>
			<iframe :src="targetStreamUrl" frameborder="0" scrolling="no" v-show="targetedStream !== ''"></iframe>
		</div>
		<div class='twitchChat'>
			<iframe frameborder="0" scrolling="no" :src="targetChatUrl" v-show="targetedStream !== ''"></iframe>
		</div>
	</div>
</div>
</template>

<script>
// import that global data store
import { mapGetters, mapActions } from 'vuex'

export default {
	name: 'twitch-embed',
	data () {
		return {
			targetedStream: this.target ? this.target : ''
		}
	},
	methods: {
		setTargetStream: function (channel) {
			this.targetedStream = channel
		},
		isSelected (channel) {
			return this.targetedStream.toLowerCase() === channel.toLowerCase()
		},
		...mapActions([
			'updateChannelInfoList'
		])
	},
	computed: {
		targetStreamUrl () {
			return this.targetedStream === '' ? '' : 'https://player.twitch.tv/?channel=' + this.targetedStream
		},
		targetChatUrl () {
			return this.targetedStream === '' ? '' : 'https://www.twitch.tv/' + this.targetedStream.toLowerCase() + '/chat'
		},
		// return a list of the live streams in a proper order
		liveStreamList () {
			var liveCollection = []
			var offlineCollection = []
			// Sort the collections into online and offline
			for (var channel in this.$store.state.TwitchInfo) {
				if (this.$store.state.TwitchInfo[channel].live) {
					liveCollection.push(this.$store.state.TwitchInfo[channel])
				} else {
					offlineCollection.push(this.$store.state.TwitchInfo[channel])
				}
			}
			// sort the live streams
			liveCollection.sort(function (a, b) {
				return b.live.viewers - a.live.viewers
			})
			// then tack on the remainders
			return liveCollection.concat(offlineCollection)
		},
		...mapGetters([
			'twitchInfo'
		])
	},
	props: ['target'],
	mounted () {
		this.updateChannelInfoList().then(function () {
			console.log(this.target)
			this.targetedStream = this.target ? this.target : this.liveStreamList[0].display_name
		}.bind(this))
	}
}
</script>

<style scoped lang="scss">
@import "../../variables.scss";
.live {
	line-height: initial;
	display: inline-block;
}
.svg-glyph_live {
	vertical-align: middle;
}
.twitchWrapper {
	position: relative;
	background-color: $gray-dark;
}
.twitchStream {
	position: relative;
	width:75%;
}
.twitchStream:before{
	content: "";
	display: block;
	padding-top: 56.25%; /* 16:9 */
}
.twitchChat{
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	width: 25%;
}
.twitchStream iframe, .twitchChat iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.wrapper{
	display: block;
	position: relative;
}
li {
	display: inline-block;
	padding: 5px 10px;
	cursor: pointer;
	line-height: 30px;
	transition: all .2s ease-in-out;
	vertical-align: middle;
	&:hover{
		background-color: $gray-light;
	}
	&:active{
		background-color: $gray-dark;
	}
}
a:not(:last-child) li{
	border-right: 1px solid $font-white;
}
.selected, .selected:hover{
	background-color: $gray-light;
	transition: all .1s ease-in-out;
}
ul {
	display: block;
	list-style-type: none;
	margin: auto;
	padding: 0;
	background-color: $gray-med;
}
@media (max-width: 1024px) {
	.twitchStream{
		width:100%;
	}
	.twitchChat{
		display:none;
	}
}
@media screen and (max-width: 45.000em){
	.not-live{
		line-height: 59px;
	}
	.live{
		display: block;
	}
}
</style>



