<template>
	<div class="date-tile">
		<div class="date-slug">
			<div class="yellow">{{weekDate}}</div>
			<h2 class="date-month">{{day}} {{month}}</h2>
		</div>
		<div class="date-event-wrap">
			<div class="date-event" v-for="event in date" :class="getEventType(event)" v-show="event.show">
				<span v-show="!event.all_day">{{getTime(event)}}</span>
				<span class="title">{{event.title}}</span>
			</div>
		</div>
	</div>
</template>

<script>
import Moment from 'moment'
import Members from '../../../static/memberInfo.json'
export default {
	name: 'date-tile',
	computed: {
		weekDate: function () {
			return Moment(this.dateString).format('dddd').toUpperCase()
		},
		day: function () {
			return Moment(this.dateString).format('D')
		},
		month: function () {
			return Moment(this.dateString).format('MMM').toUpperCase()
		}
	},
	methods: {
		getEventType: function (event) {
			for (var key in Members) {
				if (event.subcalendar_ids[0] === 2718538) {
					return 'team-event'
				}
				if (Members[key].calendar_id === event.subcalendar_ids[0]) {
					return key
				}
			}
			return 'unknown'
		},
		getTime: function (event) {
			return Moment(event.start_dt).format('LT')
		}
	},
	props: ['date', 'dateString']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
.date{
	&-tile{
		display: table-cell;
		text-align: left;
	}
	&-month{
		color: $gray-light;
	}
	&-slug{
		display: table-cell;
		min-width: 105px;
		border-right: solid 1px $yellow-dark;
		flex: 1;
	}
	&-event-wrap{
		display: table-cell;
		vertical-align: top;
		min-width: 260px;
		padding: 0 0 0 7px;
	}
}
.unknown {
	display: none;
}
.title {
  text-transform:capitalize;
  text-shadow:
    1px 1px 0 $gray-dark,
    -1px -1px 0 $gray-dark,  
    1px -1px 0 $gray-dark,
    -1px 1px 0 $gray-dark,
    1px 1px 0 $gray-dark,
}
.lowco2525 .title{
	color: $lowco-purple;
}
.smashley .title{
	color: $smashley-blue;
}
.djtechlive .title{
	color: $dj-blue;
}
.bloodyfaster .title{
	color: $bloody-red;
}
.team-event{
	color: $yellow-dark;
}
</style>


