<template>
	<a :href="stream.url" target="_blank">
		<div class="member-tile" :id="stream.display_name+'-tile'">
			<img :src="stream.logo">
			<div class="info-section">
				<h2>{{stream.display_name}}</h2>
				<p>{{description}}</p>
				<h3>{{stream.url.slice(12)}}</h3>
				<div class="schedule" v-show="!stream.live">{{schedule}}</div>
				<div class="schedule" v-show="!stream.live && schedule2 != null">{{schedule2}}</div>
				<div class="live" v-show="stream.live">live! {{viewers}}
          <svg height="16px" version="1.1" viewBox="0 0 16 16" width="16px" x="0px" y="-10px" class="svg-glyph_live">
						<path clip-rule="evenodd" d="M11,14H5H2v-1l3-3h2L5,8V2h6v6l-2,2h2l3,3v1H11z" fill-rule="evenodd"></path>
					</svg>
        </div>
			</div>
		</div>
	</a>
</template>

<script>
import config from '../../../config/config.json'
export default {
	name: 'member-tile',
	computed: {
		description: function () {
			return config.team_members[this.stream.display_name.toLowerCase()].description
		},
		schedule: function () {
			return config.team_members[this.stream.display_name.toLowerCase()].schedule
		},
		schedule2: function () {
			return config.team_members[this.stream.display_name.toLowerCase()].schedule2
		},
		viewers: function () {
			return this.stream.live ? this.stream.live.viewers : 0
		}
	},
	props: ['stream']
}
</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../variables.scss";
img{
	border-radius: 100px;
	width: 135px;
	border: 2px solid $gray-light;
	padding: 3px;
	margin-right: 15px;
	transition: all .2s ease-in-out;
}
p{
	margin: 5px 0;
}
.svg-glyph_live {
	vertical-align: middle;
}
.live{
	margin-top: 2px;
}
.member-tile{
	display: inline-block;
	margin: 25px 2%;
	padding: 10px;
	border-radius: 10px;
	transition: all .2s ease-in-out;
}
.member-tile:hover {
	background-color: $gray-dark;
	box-shadow: 0 0 2px $gray-light;
}
#lowco2525-tile:hover {
	img {
		border-color: $lowco-purple;
	}
}
#smashley-tile:hover {
	img {
		border-color: $smashley-blue;
	}

}
#djtechlive-tile:hover {
	img {
		border-color: $dj-blue;
	}

}
#bloodyfaster-tile:hover {
	img {
		border-color: $bloody-red;
	}
}
.info-section{
	display: inline-block;
	vertical-align: top;
	text-align: left;
	max-width: 325px;
}
.schedule{
	white-space: nowrap;
}
@media screen and (max-width: 80.000em){ /*960px*/
	.member-tile{
		margin: 25px 1%;
	}
	.info-section{
		max-width: 275px;
	}
}
</style>


